
function add_img(id_img) {
    document.getElementById("id_picture").onchange = function () {
        //create an element FileReader, allow the web app read files
        var reader = new FileReader();
    
        reader.onload = function (e){
            if (e.target.result ) {
                var img = document.getElementById(id_img);
                img.classList.add('show')
            };
            document.getElementById(id_img).src = e.target.result;
        }
        reader.readAsDataURL(this.files[0])
    }
}

img = document.getElementById('image')
img_complete = document.getElementById('img_complete')
if (img) {
    add_img('image')
}
if (img_complete){
    add_img('img_complete')
}








